import ImageLoader from './ImageLoader'
import data from '../json/gallery.json'
import Swiper from 'swiper';
import preloaderAnimation from './preloader'
import videoController from './video-controller'


window.onload = function() {
    preloaderAnimation()

    //main-video
    let videoMain = document.querySelector('.video-main')
    let videoSecondary = document.querySelector('.video-secondary')
   
    //play video then page is loaded
    if(videoSecondary) {
        setTimeout(() => {
            videoMain.play()
            videoSecondary.play()

            videoSecondary.addEventListener('playing', () => {
                setTimeout(() => {
                    videoSecondary.style.zIndex = "-99"
                    videoSecondary.pause()
                    videoSecondary.currentTime = 0
                },2500)
            })
        }, 2500)  
    }

    //video controlls
    videoController()

    //hide shadow after load
    setTimeout(() => {
        document.querySelector('.shadow').classList.remove('shadow--active')
    }, 250)
    
    let mainSwiper = new Swiper('.swiper-container' , {
        slidesPerView: 3,
        spaceBetween: 40,
        speed: 500,
        loop: true,
    })

    //get patametrs for redirect
    let getParams = window
        .location
        .search
        .replace('?','')
        .split('&')
        .reduce(
            function(p,e){
                var a = e.split('=');
                p[ decodeURIComponent(a[0])] = decodeURIComponent(a[1]);
                return p;
            },
            {}
        );

    //gallery
    if(document.querySelector('.gallery__container')) {
        let gallery = new ImageLoader(data, '.gallery__container', '.picture', '.picture__container', getParams)
    }

    //stop default redirect
    document.querySelectorAll('a').forEach(link => {
        link.addEventListener('click', function(e) {
            e.preventDefault()

            let initialPage = this.getAttribute('href')
            document.querySelector('.shadow').classList.add('shadow--active')
        
            setTimeout(() => {
            location.href = initialPage
            }, 250)
        })
    })
}
